$grey-900: #212529;

$white-color: #ffffff !default;
$dark-white-color: #e9e7e7 !default;
$dark-white-color-1: #f4f4f4 !default;
$black-color: #000000 !default;


// Theme colors

$code-bg-color-light: rgba($purple-color, 0.05);
$code-bg-color-dark: #2c3237 !default;