/*******************************************************************************
 * Styles for the base elements of the theme.
 ******************************************************************************/

// Typography

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&family=Source+Serif+4:wght@500;600&display=swap');

body {
 font-family: 'Inter', sans-serif;
 font-size: 1rem;
}

h1, h2, h3, h4 {
 font-family: 'Source Serif 4', serif;
}

.navbar, .footer {
 font-family: 'Inter', sans-serif;
}

p, h1, h2, h3, h4, h5, h6, em, div, li, span, strong {
 color: var(--global-text-color);
}

hr {
 border-top: 1px solid var(--global-divider-color);
}

table {
 td, th {
 color: var(--global-text-color);
 }
 td {
 font-size: 1rem;
 }
}

a, table.table a {
 color: var(--global-link-color);
 &:hover {
 color: var(--global-hover-color);
 text-decoration: none;
 }
 &:hover:after :not(.nav-item.dropdown) {
 width: 100%;
 }
}

figure, img {
 max-width: 70%;
 display: block;
 margin-left: auto;
 margin-right: auto;
}

.special {
 color: var(--global-theme-color) !important;
}

.nav-title-col {
 color: var(--global-theme-color) !important;
// font-size: x-large;
}

.highlight {
 color: var(--global-hcolor) !important;
}

.special-font {
 font-size: 1.3rem;
 font-weight: 560;
 color: #003366; /* Professional academic blue */
 margin-bottom: 5px;
}

blockquote {
 background: var(--global-bg-color);
 border-left: 2px solid var(--global-theme-color);
 margin: 1.5em 10px;
 padding: 0.5em 10px;
 font-size: 1.2rem;
}

// Math

.equation {
 margin-bottom: 1rem;
 text-align: center;
}

// Caption

.caption {
 font-size: 0.875rem;
 margin-top: 0.75rem;
 margin-bottom: 1.5rem;
 text-align: center;
}

// Card

.card {
 background-color: var(--global-card-bg-color);

 img {
 width: 100%;
 }

 .card-title {
 color: var(--global-text-color);
 }

 .card-item {
 width: auto;
 margin-bottom: 10px;

 .row {
 display: flex;
 align-items: center;
 }
 }
}

// Citation

.citation, .citation-number {
 color: var(--global-theme-color);
}

// Profile

.profile {
 margin-left: 1rem;
 width: 100%;

 .address {
 margin-bottom: 5px;
 margin-top: 5px;
 font-family: monospace;
 p {
 display: inline-block;
 margin: 0;
 }
 }
}

@media (min-width: 576px) {
 .profile {
 width: 30%;
 .address {
 p { display: block; }
 }
 }
}

.post-description {
 margin-bottom: 2rem;
 font-size: 0.875rem;
 a {
 color: inherit;
 &:hover {
 color: var(--global-hover-color);
 text-decoration: none;
 }
 }
}


// Navbar customization

.navbar {
 box-shadow: none;
 border-bottom: 1.5px solid var(--global-divider-color);
 background-color: var(--global-nav-bg-color);
 opacity: 0.9;
}
.navbar .dropdown-menu {
 background-color: var(--global-bg-color);
 border: 1px solid var(--global-divider-color);
 a:not(.active) {
 color: var(--global-nav-text-color);
 }
 a:hover {
 color: var(--global-hover-color);
 }
 .dropdown-divider {
 border-top: 1px solid var(--global-divider-color) !important;
 }
}
.dropdown-item {
 color: var(--global-nav-text-color);
 &:hover {
 color: var(--global-hover-color);
 background-color: var(--global-bg-color);
 }
}
.navbar.navbar-light {
 a {
 &:hover {
 text-decoration: none;
 }
 }
 .navbar-brand {
 color: var(--global-nav-text-color);
 }
 .navbar-nav .nav-item .nav-link {
 color: var(--global-nav-text-color);
 &:hover {
 // color: var(--global-hover-color);
 color: var(--global-active-nav-color);

 }
 }
 .navbar-nav .nav-item.active>.nav-link {
 background-color: inherit;
 font-weight: 500;
 color: var(--global-active-nav-color);
 &:hover {
 color: var(--global-active-nav-color);
 }
 }
 .navbar-brand.social {
 padding-bottom: 0;
 padding-top: 0;
 font-size: 1.7rem;
 a {
 i::before {
 color: var(--global-nav-text-color);
 transition-property: all 0.2s ease-in-out;
 }
 &:hover {
 i::before {
 color: var(--global-hover-color);
 }
 }
 }
 }
}

.navbar-toggler {
 .icon-bar {
 display: block;
 width: 22px;
 height: 2px;
 background-color: var(--global-nav-text-color);
 border-radius: 1px;
 margin-bottom: 4px;
 transition: all 0.2s;
 }
 .top-bar {
 transform: rotate(45deg);
 transform-origin: 10% 10%;
 }
 .middle-bar {
 opacity: 0;
 }
 .bottom-bar {
 transform: rotate(-45deg);
 transform-origin: 10% 90%;
 }
}

.navbar-toggler.collapsed {
 .top-bar {
 transform: rotate(0);
 }
 .middle-bar {
 opacity: 1;
 }
 .bottom-bar {
 transform: rotate(0);
 }
}

#light-toggle {
 padding: 0;
 border: 0;
 background-color: inherit;
 color: var(--global-text-color);
 &:hover {
 color: var(--global-hover-color);
 }
}

// Social (bottom)

.social {
 text-align: center;
 .contact-icons {
 font-size: 3rem;
 a {
 i::before {
 color: var(--global-text-color);
 transition-property: all 0.2s ease-in-out;
 }
 &:hover {
 i::before {
 color: var(--global-hover-color);
 }
 }
 }
 }
 .contact-note {
 font-size: 0.8rem;
 }
}


// Footer
footer.fixed-bottom {
 background-color: var(--global-footer-bg-color);
 font-size: 0.75rem;
 .container {
 color: var(--global-footer-text-color);
 padding-top: 9px;
 padding-bottom: 8px;
 }
 a {
 color: var(--global-footer-link-color);
 &:hover {
 color: var(--global-hover-color);
 text-decoration: none;
 }
 }
}

footer.sticky-bottom {
 border-top: 1px solid var(--global-divider-color);
 padding-top: 40px;
 padding-bottom: 40px;
 font-size: 0.9rem;
}

.list-group-item {
 background-color: transparent; /* no background */
 border: none; /* no full border */
 margin-bottom: 10px;

 .badge {
 color: var(--global-text-color) !important;
 background-color: var(--global-card-bg-color) !important;
 border: 1px solid var(--global-text-color) !important;
 }
}

// CV

.cv {
 margin-bottom: 40px;
 
 .card {
 background-color: var(--global-card-bg-color);
 border: 1px solid var(--global-divider-color);
 
 .list-group-item {
 background-color: inherit;

 .badge {
 color: var(--global-text-color) !important;
 background-color: var(--global-card-bg-color) !important;
 border: 1px solid var(--global-text-color) !important;
 }
 }
 }
}

// Blog

.header-bar {
 border-bottom: 1px solid var(--global-divider-color);
 text-align: center;
 padding-top: 2rem;
 padding-bottom: 5rem;
 h1 {
 color: var(--global-theme-color);
 font-size: 5rem;
 }
}

.post-list {
 margin: 0;
 margin-bottom: 40px;
 padding: 0;
 li {
 border-bottom: 1px solid var(--global-divider-color);
 list-style: none;
 padding-top: 2rem;
 padding-bottom: 2rem;
 .post-meta {
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 margin-bottom: 0;
 }
 .post-tags {
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 padding-top: 0.25rem;
 padding-bottom: 0;
 }
 a {
 color: var(--global-text-color);
 text-decoration: none;
 &:hover {
 color: var(--global-hover-color);
 }
 }
 }
}

.pagination {
 .page-item {
 .page-link {
 color: var(--global-text-color);
 &:hover {
 color: $black-color;
 }
 }
 &.active .page-link {
 color: $white-color;
 background-color: var(--global-theme-color);
 &:hover {
 background-color: var(--global-hover-color);
 }
 }
 }
}


// Distill

.distill {
 a:hover {
 border-bottom-color: var(--global-hover-color);
 text-decoration: none;
 }
}


// Projects

.projects {
 a {
 text-decoration: none;

 &:hover {
 .card-title {
 color: var(--global-hover-color);
 }
 }
 }

 .card {
 img {
 width: 100%;
 }
 }

 .card-item {
 width: auto;
 margin-bottom: 10px;

 .row {
 display: flex;
 align-items: center;
 }
 }

 .grid-sizer, .grid-item {
 width: 250px;
 margin-bottom: 10px;
 }

 h2.category {
 color: var(--global-divider-color);
 border-bottom: 1px solid var(--global-divider-color);
 padding-top: 0.5rem;
 margin-top: 2rem;
 margin-bottom: 1rem;
 text-align: right;
 }
}


// Publications

.publications {
 margin-top: 2rem;
 h1 {
 color: var(--global-theme-color);
 font-size: 2rem;
 text-align: center;
 margin-top: 1em;
 margin-bottom: 1em;
 }
 h2 {
 margin-bottom: 1rem;
 span {
 font-size: 1.5rem;
 }
 }
 h2.year {
 color: var(--global-divider-color);
 border-top: 1px solid var(--global-divider-color);
 padding-top: 1rem;
 margin-top: 2rem;
 margin-bottom: -2rem;
 text-align: right;
 }
 ol.bibliography {
 list-style: none;
 padding: 0;
 margin-top: 0;

 li {
 margin-bottom: 1rem;
 .abbr {
 height: 2rem;
 margin-bottom: 0.5rem;
 abbr {
 display: inline-block;
 background-color: var(--global-theme-color);
 padding-left: 1rem;
 padding-right: 1rem;
 a {
 color: white;
 &:hover {
 text-decoration: none;
 }
 }
 }
 .award {
 color: var(--global-theme-color) !important;
 border: 1px solid var(--global-theme-color);
 }
 }
 .title {
 font-weight: bolder;
 }
 .author {
 a {
 border-bottom: 1px dashed var(--global-theme-color);
 &:hover {
 border-bottom-style: solid;
 text-decoration: none;
 }
 }
 > em {
 border-bottom: 1px solid;
 font-style: normal;
 }
 }
 .links {
 a.btn {
 color: var(--global-text-color);
 border: 1px solid var(--global-text-color);
 padding-left: 1rem;
 padding-right: 1rem;
 padding-top: 0.25rem;
 padding-bottom: 0.25rem;
 &:hover {
 color: var(--global-hover-color);
 border-color: var(--global-theme-color);
 }
 }
 }
 .hidden {
 font-size: 0.875rem;
 max-height: 0px;
 overflow: hidden;
 text-align: justify;
 transition-property: 0.15s ease;
 -moz-transition: 0.15s ease;
 -ms-transition: 0.15s ease;
 -o-transition: 0.15s ease;
 transition: all 0.15s ease;

 p {
 line-height: 1.4em;
 margin: 10px;
 }
 pre {
 font-size: 1em;
 line-height: 1.4em;
 padding: 10px;
 }
 }
 .hidden.open {
 max-height: 100em;
 transition-property: 0.15s ease;
 -moz-transition: 0.15s ease;
 -ms-transition: 0.15s ease;
 -o-transition: 0.15s ease;
 transition: all 0.15s ease;
 }
 div.abstract.hidden {
 border: dashed 1px var(--global-bg-color);
 }
 div.abstract.hidden.open {
 border-color: var(--global-text-color);
 }
 }
 }
}

// Rouge Color Customization
figure.highlight {
 margin: 0 0 1rem;
}

pre {
 color: var(--global-theme-color);
 background-color: var(--global-code-bg-color);
 border-radius: 6px;
 padding: 6px 12px;
 pre, code {
 background-color: transparent;
 border-radius: 0;
 padding: 0;
 }
}

code {
 color: var(--global-theme-color);
 background-color: var(--global-code-bg-color);
 border-radius: 3px;
 padding: 3px 3px;
}


// Transitioning Themes
html.transition,
html.transition *,
html.transition *:before,
html.transition *:after {
 transition: all 750ms !important;
 transition-delay: 0 !important;
}

// Extra Markdown style (post Customization)
.post{
 .post-meta{
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 margin-bottom: 0;
 }
 .post-tags{
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 padding-top: 0.25rem;
 padding-bottom: 1rem;
 a {
 color: var(--global-text-color-light);
 text-decoration: none;
 &:hover {
 color: var(--global-hover-color);
 }
 }
 }
 .post-content{
 blockquote {
 border-left: 5px solid var(--global-theme-color);
 padding: 8px;
 }
 }
}

.badge-new {
 color: #d32f2f;
 font-size: 0.88em;
 font-weight: 500;
}

//
// General custom styles
//

.section-title {
 color: var(--global-header-color);
 font-size: 1.1rem;
 font-weight: 500;
 margin-top: 1rem;
 border-bottom: 2px solid #e0e0e0;
}

.section-title-mid {
 color: var(--global-header-color);
 font-size: 1.5rem;
 font-weight: 500;
 margin-top: 1rem;
 text-align: center;
}

//
// Social icons style
//
.social-icons {
 display: flex;
 justify-content: center; /* center horizontally */
 flex-wrap: wrap; /* wrap if too many */
 gap: 15px; /* spacing between icons */
 margin: 20px 0; /* optional spacing */
}

.social-icons a {
 font-size: 27px; /* bigger icons */
 color: var(--global-link-color); /* default color */
 transition: color 0.3s;
}

.social-icons a:hover {
 color: var(--global-hover-color); /* hover color */
}

//
// Publication specific styles
//

.paper-title {
 font-size: 1.15rem;
 margin-bottom: 0.17rem;
 line-height: 1.3;
 color: #000;
}

.authors {
 font-size: 0.9rem;
 margin-bottom: 0.15rem;
 font-family: -apple-system, BlinkMacSystemFont, "Segoe UI",
 Roboto, "Helvetica Neue", Arial, sans-serif
}

.author-font {
 font-weight: 400;
}

.venue {
 display: inline-block;
 font-weight: 500;
 letter-spacing: 0.25px;
 // margin-top: 0.17rem;
 // margin-bottom: 0.17rem;
 // background-color: #ebeaec;
 // padding: 2px 6px;
 // border-radius: 4px;
 font-size: 0.85em;
}

.talk_venue {
 font-size: 0.84rem;
 color: #6B7280;
 margin-bottom: 0.17rem;
}

.small-slash {
 font-size: 0.9em;
}

.paper-award {
 margin-top: 1px;
}

.paper-award-badge {
 color: $red-color;
 font-size: 0.93rem;
 vertical-align: middle;
}

.paper-award-badge:hover {
 color: $red-color;
}

.paper-links {
 font-size: 0.87rem;
 margin-top: 5px;
}

.abstract-block {
 background-color: $dark-white-color-1;
 padding: 0.75rem 1rem;
 border-radius: 6px;
 margin-top: 0.5rem;
 transition: all 0.3s ease;
 }
 
.abstract-block p {
 margin: 0;
 font-size: 0.93rem;
}

.bibtex-block pre {
 background-color: $dark-white-color-1;
 color: #333; /* readable text color */
 padding: 0.75rem 1rem;
 border-radius: 6px;
 font-family: monospace;
 font-size: 0.9rem;
 position: relative;
 overflow-x: auto;
 white-space: pre-wrap;
 }

//
// Awards specific styles
//

.award-title {
 font-size: 1rem;
}

.award-links {
 font-size: 0.95rem;
 padding-top: 2px;
}